import axios from "axios"
export async function getbooker() {
  //sessionStorage.getItem('StaffNo')"62018600268305"
  // https://cwellsapi.casonline.cn/apiservice/apiservice.asmx/GetPassengerBystaffnopaxnameByPost
  // https://timas.efasco.com/ErpService.asmx/GetPassengerBystaffnopaxnameByPost
  // let g = await axios.post('https://cwellsapi.casonline.cn/apiservice/apiservice.asmx/GetPassengerBystaffnopaxnameByPost',JSON.stringify({"staff_no":sessionStorage.getItem('StaffNo')}));
  let paxname = ""
  let companyjde = ""
  let g = await axios({
    type: "GET",
    url:
      //获取乘客列表
      "https://timas.efasco.com/ErpService.asmx/GetPassengerBystaffnoAES?staff_no=" +
      sessionStorage.getItem("StaffNo") +
      "&paxname=" +
      paxname +
      "&companyjde=" +
      companyjde,
    cache: "false",
    dataType: "json",
  })

  //https://timas.efasco.com/ErpService.asmx/GetPassengerBystaffnoAES
  g.data.psglist = g.data.psglist.filter((v) => {
    console.log("1111", v)
    v.idtype = "身份证"
    // console.log(v)
    v.IdCardNo = ""
    if (v.IdNo) {
      if (v.IdNo == v.PassportNo) {
        v.idtype = "护照"
      } else if (v.IdNo == v.HkmcEepNo) {
        v.idtype = "大陆居民往来港澳通行证"
      } else if (v.IdNo == v.ReentryPermitNo) {
        v.idtype = "港澳居民来往内地通行证"
      } else if (v.IdNo == v.MtpNo) {
        v.idtype = "台湾居民来往大陆通行证"
      } else {
        v.IdCardNo = v.IdNo
        // v.idtype = '身份证';
        // if(!(card&&card =='护照')){
        //     v.IdCardNo = v.IdNo;
        // }
      }
    } else {
      if (v.PassportNo) {
        v.idtype = "护照"
        v.IdNo = v.PassportNo
      } else if (v.HkmcEepNo) {
        v.idtype = "大陆居民往来港澳通行证"
        v.IdNo = v.HkmcEepNo
      } else if (v.ReentryPermitNo) {
        v.idtype = "港澳居民来往内地通行证"
        v.IdNo = v.ReentryPermitNo
      } else if (v.MtpNo) {
        v.idtype = "台湾居民来往大陆通行证"
        v.IdNo = v.MtpNo
      }
    }
    return v.Identification, v.idtype
  })
  g.data.timespan = new Date().getTime()
  // set_by_cltid(g.data.psglist[0].CltId);
  sessionStorage.setItem("psglist_json", JSON.stringify(g.data))
  // let list  =g.data.psglist;
  // sessionStorage.setItem('booker',JSON.stringify(list[0]));
  console.log(g)
  return g
}
