<template>
  <div class="indexStyle">
    <div class="indexBg">
      <van-notice-bar left-icon="volume-o" text=" 经由EASYWAY系统购买的机票/火车票/酒店，都无需向航空公司/火车站/酒店要求打印发票（机票行程单/火车票/酒店发票)！" speed="40" />
      <div class="navTopList">
        <van-tabs swipeable scrollspy class="indexNavTop" v-model:active="indexActive">
          <van-tab v-for="index in indexNavList" :title="formatTitle(index.title,index.titleEn)" :key="index" class="indexNavTitle">
            <div v-for="item in index.list" :key="item.title" class="indexNavContent" @click="getPathRouter(item.path)">
              <img :src="getImagePath(item.imgurl)" alt="" class="indexNavContentImg">
              <!-- <img src="../../assets/img/not_submitted.png" alt=""> -->
              <span class="indexNavContentChinese idnexNavChineseStyle">{{item.title}}</span>
              <span class="indexNavContentChinese">{{item.titleEn}}</span>
            </div>
          </van-tab>
        </van-tabs>
      </div>
    </div>
    <div class="indexBottom">
      <div class="bottomx">
        <div class="visa2" @click="indexDynamicBroadcasting">
          <div style="display: flex;justify-content: center;align-items: center;color: #337ab7;">
            <van-icon name="volume-o" />
            <span>动态播报 Travel Tips</span>
          </div>
        </div>
        <div class="contactus">
          <a href="tel:021962990">
            <van-icon name="phone-o" />
            在线呼叫 Call Us
          </a>
        </div>
      </div>
    </div>
    <div class="indexPopup">
      <van-popup v-model:show="indexshowCenter" class="select_popup" round :style="{ height: '50%', width: '75%' }" :close-on-click-overlay="false">
        <div class="warnPup_box">
          <div class="warnPup_title">
            <!-- <img src="../assets/image/notyT.png" alt="" /> -->
            <div class="noti_box">
              <span>!</span>
            </div>
            <p>疫情防控提醒</p>
          </div>
          <div class="warnPup_content">
            <template v-for="n in 1">
              <p v-for="(item, i) in warnText" :key="i + n">
                {{ item }}
              </p>
            </template>
          </div>
          <div class="warnPup_ntn">
            <span class="warnPup_suc" @click="indexshowCenter = false;$store.state.warnSuccess = 1;">已知晓</span>
            <span class="ntn_loo" @click="isRouter_T">查看详情</span>
          </div>
        </div>
      </van-popup>
    </div>
    <van-watermark :content="userNameChinese" z-index="-1" />

  </div>
</template>

<script>
import { getbooker } from "../../utils/getbooker.js";
import axios from '../../utils/axiosLogin.js'
import crypto from '../../utils/crypto.js'
import { showDialog } from "vant";
export default {
  name: 'index',
  data () {
    return {
      indexshowCenter: false,
      userinfo: [],
      clti: null,
      userNameChinese: localStorage.getItem('getUserNameChinese'),
      booker: JSON.parse(sessionStorage.getItem("booker")),
      filter_stat_r: ['"已提交审批"', '""已出票""'],
      filter_stat_r_x: [
        [], //已提交审批
        [], //已出票
      ],
      indexActive: 1,
      indexNavList: [
        {
          title: '申请单',
          titleEn: 'Request',
          list: [
            {
              title: '未提交申请',
              titleEn: 'Unsubmit Request',
              imgurl: 'not_submitted.png',
              path: '/applicationform?active=0'
            },
            {
              title: '待审批',
              titleEn: 'Pending Approval',
              imgurl: 'pending_approval.png',
              path: '/applicationform?active=1'
            },
            {
              title: '已审批',
              titleEn: 'Approved',
              imgurl: 'approved.png',
              path: '/applicationform?active=2'
            },
            {
              title: '已取消',
              titleEn: 'Canceled',
              imgurl: 'canceled.png',
              path: '/applicationform?active=3'
            }
          ]
        },
        {
          title: '新增',
          titleEn: 'New',
          list: [
            {
              title: '国内机票',
              titleEn: 'Domestic Air',
              imgurl: 'domestic_flight.png',
              path: '/flight'
            },
            {
              title: '国际机票',
              titleEn: 'International Air',
              imgurl: 'international_flight.png',
              path: '/internationalflight'
            },
            {
              title: '高铁火车',
              titleEn: 'Train',
              imgurl: 'train.png',
              path: '/train'

            },
            {
              title: '酒店住宿',
              titleEn: 'Hotel',
              imgurl: 'hotel.png',
              path: '/hotel'

            },
            {
              title: '签证 ',
              titleEn: 'Vasa',
              imgurl: 'visa.png',
              path: '/visa'

            }
          ]
        },
        {
          title: '审批单',
          titleEn: 'Approval',
          list: [
            {
              title: '待审批',
              titleEn: 'Pending Approval',
              imgurl: 'pending_approval.png',
              path: '/approveform?active=0'
            },
            {
              title: '已审批',
              titleEn: 'Approved',
              imgurl: 'approved.png',
              path: '/approveform?active=1'

            },
            {
              title: '已驳回',
              titleEn: 'Rejected',
              imgurl: 'canceled.png',
              path: '/approveform?active=2'
            }
          ]
        },
        {
          title: '我的',
          titleEn: 'My',
          list: [
            {
              title: '设置',
              titleEn: 'Setting Up',
              imgurl: 'pending_approval.png',
              path: '/login'
            },
          ]
        },
        {
          title: '国内机票',
          titleEn: 'Domestic Air',
          list: [
            {
              title: '国内机票',
              titleEn: 'Domestic Air',
              imgurl: 'domestic_flight.png',
              path: '/flight'
            },
          ]
        },
        {
          title: '高铁火车',
          titleEn: 'Train',
          list: [
            {
              title: '高铁火车',
              titleEn: 'Train',
              imgurl: 'train.png',
              path: '/train'
            },
          ]
        }
      ],
      warnText: [
        "为有效防范疫情风险，各地防疫防控措施适时调整。FASCO提醒近期有差旅需求的旅客，在临出发前再次确认目的地省市（含转机地）的限制及防控要求，以免影响您的正常出行。",
      ],


    }
  },
  computed: {
    // ...mapState(['userNameChinese'])
  },
  watch: {
    "$store.state.choose_psg": {
      handler (e, ne) {
        let a = this.$store.state.CltId_set;
        console.log(
          "舱位限制",
          e.length,
          a.f_cabin_choose_by_psglevel,
          e[0].ServiceLevelF
        );

        if (e.length > 0 && a.f_cabin_choose_by_psglevel) {
          if (e[0].ServiceLevelF == "F") {
            this.cabins = [
              "经济舱/Economy Class",
              "公务舱/Business Class",
              "头等舱/First Class",
            ];
          } else if (e[0].ServiceLevelF == "C") {
            this.cabins = ["经济舱/Economy Class", "公务舱/Business Class"];
          } else if (e[0].ServiceLevelF == "Y") {
            this.cabins = ["经济舱/Economy Class"];
          }

          if (this.cabin && this.cabins.indexOf(this.cabin) == -1) {
            this.cabin = this.cabins[0];
          }
        }
      },
    },
  },
  created () {

    // setTimeout(() => {
    // this.indexshowCenter = true

    // }, 800);
    if (sessionStorage.getItem("choose_flight")) {
      sessionStorage.removeItem("choose_flight");
      sessionStorage.removeItem("savelist_0");
      sessionStorage.removeItem("savelist_1");
      sessionStorage.removeItem("getlist_0");
      sessionStorage.removeItem("getlist_1");
    }
    // 获取管理员信息
    this.userinfo = JSON.parse(sessionStorage.getItem("booker"));

    this.$store.commit("setHistory");
    let old_booker = null;
    let limit = 10;
    // this.$store.commit("CltId_set", CltId_set);
    if (sessionStorage.getItem("psglist_json") && this.userinfo) {
      old_booker = JSON.parse(sessionStorage.getItem("psglist_json"));
      let now = new Date().getTime();
      if (now - old_booker.timespan > limit * 60000) {
        this.reload_booker(true).then(() => {
          this.get_my_trip();
        });
      } else {
        this.get_my_trip();
      }
    } else {
      this.reload_booker(true).then(() => {
        this.get_my_trip();
      });
    }
    if (!sessionStorage.getItem('StaffNo') || !this.userinfo || !this.booker.CltId) {
      this.$router.push('/login')
    }
    this.clti = this.$puilc.set_cltid(this.booker.CltId);

    sessionStorage.setItem("set_cltid", JSON.stringify(this.clti));
    axios({
      method: "GET",
      url:
        "https://timas.efasco.com/ErpService.asmx/GetCostcenterByClientId?cltid=" +
        this.booker.CltId,
      cache: "false",
      dataType: "json",
    }).then((res) => {
      sessionStorage.setItem("cclist", JSON.stringify(res.data));
    });
  },
  mounted () {

  },
  // components: {
  //   Watermark,
  //   Tab,
  //   Tabs
  // },
  methods: {
    reload_booker (bool) {
      const booker = getbooker();
      return booker;
    },
    isRouter_T () {
      window.location.href =
        "http://m.sh.bendibao.com/news/hesuanjiance/all.php?leavecity=";
    },
    indexDynamicBroadcasting () {
      // this.$router.push('/dynamic')
      window.location.href =
        "https://ezway.efasco.com/wxapp/Industry/index.html";
    },
    formatTitle (title, titleEn) {
      return `${title}-${titleEn}`
    },
    getImagePath (imgurl) {
      return require(`../../assets/img/${imgurl}`);
    },
    // 路由跳转
    getPathRouter (path) {
      console.log(path);
      if (path == '/login') {
        showDialog({
          title: '提示/Notice',
          showCancelButton: true,
          cancelButtonText: '取消/Cancel',
          confirmButtonText: '确认/Confirm',
          message:
            '确认退出登录吗？/ Are you sure to log out?',
          className: 'logOutButtonStyle'
        })
          .then(() => {
            // on confirm
            this.$router.push('/login')
          })
          .catch(() => {
            // on cancel
          });
      } else if (path == '/flight' || path == '/internationalflight' || path == '/train' || path == '/hotel') {
        this.$router.push(path)
      } else if (path == '/visa') {
        //跳转外链
        window.location.href =
          "https://timas.efasco.com/visa/visa_h5/visa_h5.html";
        //打开新的标签页
        // window.open("https://timas.efasco.com/visa/visa_h5/visa_h5.html");
      } else if (path == '/applicationform?active=0' || path == '/applicationform?active=1' || path == "/applicationform?active=2" || path == '/applicationform?active=3') {
        this.$router.push(path)
      } else if (path == '/approveform?active=0' || path == '/approveform?active=1' || path == '/approveform?active=2') {
        this.$router.push(path)
      }
    },
    async get_my_trip () {
      let end = new Date(
        new Date().getFullYear(),
        new Date().getMonth(),
        new Date().getDate() + 1
      ).format("yyyy-MM-dd");
      let myDate = new Date();
      let start = new Date(myDate.getTime()).format("yyyy-MM-dd");
      let paxname = "";
      let orderno = "";
      let companyjde;
      let lw = new Date(myDate.getTime() - 1000 * 60 * 60 * 24 * 30).format(
        "yyyy-MM-dd"
      );

      let f = await Promise.all([axios(
        {
          type: "GET",
          url:
            //获取乘客列表
            "https://timas.efasco.com/ErpService.asmx/GetPassengerBystaffnoAES?staff_no=" +
            this.booker.StaffNo +
            "&paxname=" +
            paxname +
            "&companyjde=" +
            companyjde,
          cache: "false",
          dataType: "json",
        }
      ),

      axios.get(
        // timas.efasco.com/ErpService.asmx/GetNeedAuthTripsByNo
        //cwellsapi.casonline.cn/apiservice/apiservice.asmx/GetNeedAuthTripsByNo

        "https://ezway.efasco.com/ErpService.asmx/GetNeedAuthTripsByNo?staffno=" +
        this.booker.StaffNo +
        "&start=" +
        lw +
        "&end=" +
        end +
        "&paxname=" +
        paxname +
        "&orderno=" +
        orderno
      ),
        null,
      ]);
      //乘客列表
      this.filter_stat_rule_approve(f[1].data);
      sessionStorage.setItem("psglist_json", JSON.stringify(f[0].data));
      //旅客列表数据
      let psglistx = f[0].data.psglist;
      let card = "";
      // console.log(psglistx);
      for (let i = psglistx.length - 1; i >= 0; i--) {
        if (!psglistx[i].Identification && !psglistx[i].FltPaxnameDom) {
          psglistx.splice(i, 1);
          // console.log(1111);
        }
      }
      //旅客列表数据处理
      for (let i = 0; i < psglistx.length; i++) {
        psglistx[i].IdNo = crypto.decrypt(psglistx[i].IdNo);
        psglistx[i].IdCardNo = crypto.decrypt(psglistx[i].IdCardNo);
        psglistx[i].HkmcEepNo = crypto.decrypt(psglistx[i].HkmcEepNo);
        psglistx[i].ReentryPermitNo = crypto.decrypt(
          psglistx[i].ReentryPermitNo
        );
        psglistx[i].PassportNo = crypto.decrypt(psglistx[i].PassportNo);
        psglistx[i].MtpNo = crypto.decrypt(psglistx[i].MtpNo);
        psglistx[i].OtherIdNo = crypto.decrypt(psglistx[i].OtherIdNo);
        psglistx[i].IdCardNo = psglistx[i].IdNo;
        psglistx[i].IdCardValidDate = "";
        if (psglistx[i].IdNo) {
          if (psglistx[i].IdNo == psglistx[i].PassportNo) {
            psglistx[i].idtype = "护照";
          } else if (psglistx[i].IdNo == psglistx[i].HkmcEepNo) {
            psglistx[i].idtype = "港澳通行证";
          } else if (psglistx[i].IdNo == psglistx[i].ReentryPermitNo) {
            psglistx[i].idtype = "港澳居民来往内地通行证";
          } else if (psglistx[i].IdNo == psglistx[i].MtpNo) {
            psglistx[i].idtype = "台湾居民来往大陆通行证";
          } else {
            psglistx[i].idtype = card && card == "护照" ? "护照" : "身份证";
            if (!(card && card == "护照")) {
              psglistx[i].IdCardNo = psglistx[i].IdNo;
            }
          }
        }
        if (card && card == "护照") {
          if (!psglistx[i].PassportNo) {
            psglistx[i].idtype = "身份证";
            psglistx[i].IdNo = psglistx[i].IdCardNo;
            psglistx[i].IdNoVa = psglistx[i].IdCardValidDate;
            if (!psglistx[i].IdNo) {
              psglistx[i].idtype = "港澳通行证";
              psglistx[i].IdNo = psglistx[i].HkmcEepNo;
              psglistx[i].IdNoVa = psglistx[i].HkmcEepValidDate;
              if (!psglistx[i].HkmcEepNo) {
                psglistx[i].idtype = "港澳居民来往内地通行证";
                psglistx[i].IdNo = psglistx[i].ReentryPermitNo;
                psglistx[i].IdNoVa = psglistx[i].ReentryPermitValidDate;
                if (!psglistx[i].ReentryPermitNo) {
                  psglistx[i].idtype = "台湾居民来往大陆通行证";
                  psglistx[i].IdNo = psglistx[i].MtpNo;
                  psglistx[i].IdNoVa = psglistx[i].MtpValidDate;
                  if (!psglistx[i].MtpNo) {
                    psglistx[i].idtype = "大陆居民往来台湾通行证";
                    psglistx[i].IdNo = psglistx[i].OtherIdNo;
                    psglistx[i].IdNoVa = psglistx[i].MtpValidDate;
                  }
                }
              }
            }
          } else {
            psglistx[i].IdNo = psglistx[i].PassportNo;
            psglistx[i].IdNoVa = psglistx[i].PassportValidDate;
          }
        } else {
          if (!psglistx[i].IdCardNo) {
            psglistx[i].idtype = "护照";
            psglistx[i].IdNo = psglistx[i].PassportNo;
            if (!psglistx[i].PassportNo) {
              psglistx[i].idtype = "港澳通行证";
              psglistx[i].IdNo = psglistx[i].HkmcEepNo;
              if (!psglistx[i].HkmcEepNo) {
                psglistx[i].idtype = "港澳居民来往内地通行证";
                psglistx[i].IdNo = psglistx[i].ReentryPermitNo;
                if (!psglistx[i].ReentryPermitNo) {
                  psglistx[i].idtype = "台湾居民来往大陆通行证";
                  psglistx[i].IdNo = psglistx[i].MtpNo;
                }
              }
            }
          } else {
            psglistx[i].IdNo = psglistx[i].IdCardNo;
          }
        }
        if (psglistx[i].StaffNo == this.staffid) {
          this.bookerx = psglistx[i];
        }
      }
      let psglist = psglistx;
      sessionStorage.setItem("psglist", JSON.stringify(psglist));
      //获取用户信息

      //申请单与审批单列表接口参数
      sessionStorage.setItem(
        "home_datax",
        JSON.stringify({
          applicationurl_parameter: { start, end },
          approveurl_parameter: { start, end, paxname, orderno },
        })
      );
      // console.log('9999', this.booker, this.$store.state.CltId_set)
    },
    filter_stat_rule_approve (arr) {
      // console.log('9999', arr);
      //审批单徽标数量统计
      let n_arr = [];
      let that = this;

      // console.log(arr);

      if (!arr || arr.length <= 0) {
        return;
      }
      for (let i = arr.length - 1; i >= 0; i--) {
        if (!arr[i].Status) {
        } else {
          for (let j = 0; j < that.filter_stat_r.length; j++) {
            let ii = that.filter_stat_r[j].indexOf(arr[i].Status);
            if (ii != -1 && arr[i].Status) {
              that.filter_stat_r_x[j].unshift(arr[i]);
            }
          }
        }
        //console.log(i,arr[i].Status);
      }
      console.log('filter_stat_r_x', that.filter_stat_r_x);

      // if (that.filter_stat_r_x[0].length > 0 && booker.JobTitle == "approver") {
      //   console.log(111);
      // } else {
      //   return;
      // }
    },
  }
}
</script>

<style lang="scss" >
.indexStyle {
  height: 100vh;
  overflow: hidden;
  .indexBg {
    background: url("../../assets/img/homeIndex.png") no-repeat;
    background-size: cover;
    height: 60%;
    width: 100%;
    // border-radius: 0 0 50% 50%;
    // margin-left: -10%;
    .van-notice-bar {
      opacity: 0.5;
      font-weight: 600;
    }
    .navTopList {
      width: 100%;
      position: absolute;
      bottom: 10%;
      height: 265px;
      .indexNavTop {
        .indexNavTitle {
          white-space: pre-line;
          .van-tab__panel {
            display: flex;
            flex-wrap: wrap;
          }
          .van-tab__panel > * {
            flex: 0 0 25%; /* 每个元素占据一行的四分之一 */
            box-sizing: border-box; /* 确保元素的内边距和边框不会增加元素的宽度 */
          }
          .indexNavContent {
            margin-bottom: 40px;
            display: flex;
            flex-direction: column;
            .indexNavContentImg {
              width: 22px;
              height: 27px;
              margin: 10px auto;
            }
            .indexNavContentChinese {
              font-size: 12px;
            }
            .idnexNavChineseStyle {
              margin-bottom: 5px;
            }
          }
        }
        .van-tabs__content--animated {
          text-align: center;
          margin-top: 20px;
        }
      }
    }
  }
  .indexBottom {
    width: 100%;
    position: fixed;
    bottom: 0;
    .bottomx {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 5px 10px;
      .visa2 span {
        margin-left: 5px;
      }
      .contactus a {
        color: #337ab7;
      }
    }
  }

  // .swiper {
  //   margin: 50px auto;
  //   width: 400px;
  //   height: 400px;
  // }

  // .swiper-slide {
  //   background-color: #eee;
  //   display: flex;
  //   align-items: center;
  //   justify-content: center;
  //   font-size: 30px;
  // }
  .select_popup {
    padding: 20px 15px 0px 15px;
    box-sizing: border-box;

    .warnPup_box {
      display: flex;
      flex-direction: column;
      // justify-content: center;
      align-content: center;
      width: 100%;
      height: 100%;
      .warnPup_title {
        // height: 25%;
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-bottom: 10px;

        img {
          width: 35px;
          height: 35px;
        }

        .noti_box {
          width: 40px;
          height: 40px;
          background: linear-gradient(160deg, #f6a850 0%, #fa802b 100%);
          border-radius: 50%;
          display: flex;
          justify-content: center;
          align-items: center;
          margin-bottom: 10px;
          span {
            // text-indent: 10px;
            font-size: 25px;
            text-align: center;
            color: white;
          }
        }
        p {
          font-weight: 800;
          font-size: 18px;
        }
      }
      .warnPup_content {
        height: 70%;
        overflow: auto;
        font-size: 14px;
        padding: 10px;
        font-weight: 600;
        font-family: PingFangSC-Regular, PingFang SC;
        p {
          text-indent: 20px;
          text-align: left;
          line-height: 20px;
        }
      }
      .warnPup_ntn {
        width: 100%;
        height: 20%;
        position: relative;
        display: flex;
